/* KimiClaw Operations Dashboard - Brutalist Theme */

:root {
  --bg-primary: #0a0a0a;
  --bg-secondary: #111111;
  --bg-tertiary: #1a1a1a;
  --text-primary: #e0e0e0;
  --text-secondary: #888888;
  --text-muted: #555555;
  --accent-info: #00d4ff;
  --accent-success: #00ff88;
  --accent-warning: #ffaa00;
  --accent-danger: #ff4444;
  --border: #222222;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'JetBrains Mono', monospace;
  background: var(--bg-primary);
  color: var(--text-primary);
  line-height: 1.6;
  font-size: 14px;
}

.app {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

/* Header */
.header {
  background: var(--bg-secondary);
  border-bottom: 1px solid var(--border);
  padding: 1rem 2rem;
  display: flex;
  align-items: center;
  gap: 2rem;
}

.brand {
  display: flex;
  align-items: center;
  gap: 0.75rem;
}

.logo-mark {
  font-size: 1.5rem;
  color: var(--accent-info);
}

.brand-name {
  font-weight: 700;
  font-size: 0.875rem;
  letter-spacing: 0.05em;
}

.brand-tagline {
  font-size: 0.625rem;
  color: var(--text-muted);
  text-transform: uppercase;
}

.nav {
  display: flex;
  gap: 0.5rem;
  flex: 1;
}

.nav button {
  padding: 0.5rem 1rem;
  background: transparent;
  border: 1px solid var(--border);
  color: var(--text-secondary);
  font-family: inherit;
  font-size: 0.75rem;
  text-transform: uppercase;
  cursor: pointer;
  transition: all 0.2s;
}

.nav button:hover,
.nav button.active {
  background: var(--accent-info);
  color: var(--bg-primary);
  border-color: var(--accent-info);
}

.meta {
  font-size: 0.625rem;
  color: var(--text-muted);
}

/* Main */
.main {
  flex: 1;
  padding: 2rem;
  overflow-y: auto;
}

/* Stats Grid */
.stats-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 1rem;
  margin-bottom: 2rem;
}

.stat-card {
  background: var(--bg-secondary);
  border: 1px solid var(--border);
  padding: 1.5rem;
  text-align: center;
}

.stat-value {
  font-size: 2.5rem;
  font-weight: 700;
  color: var(--accent-info);
  line-height: 1;
}

.stat-label {
  font-size: 0.625rem;
  color: var(--text-muted);
  text-transform: uppercase;
  margin-top: 0.5rem;
  letter-spacing: 0.1em;
}

/* Sections */
.section {
  background: var(--bg-secondary);
  border: 1px solid var(--border);
  padding: 1.5rem;
  margin-bottom: 1.5rem;
}

.section h2 {
  font-size: 0.875rem;
  font-weight: 700;
  margin-bottom: 1rem;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

/* Project Cards */
.project-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 1rem;
}

.project-card {
  background: var(--bg-tertiary);
  border: 1px solid var(--border);
  padding: 1rem;
}

.project-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0.5rem;
}

.project-name {
  font-weight: 600;
}

.project-status {
  font-size: 0.625rem;
  padding: 0.125rem 0.5rem;
  text-transform: uppercase;
}

.project-status.active,
.project-status.building {
  background: var(--accent-success);
  color: var(--bg-primary);
}

.project-desc {
  font-size: 0.75rem;
  color: var(--text-secondary);
  margin-bottom: 0.75rem;
}

.project-links {
  display: flex;
  gap: 0.5rem;
  margin-bottom: 0.5rem;
}

.project-links a {
  font-size: 0.75rem;
  color: var(--accent-info);
  text-decoration: none;
}

.project-links a:hover {
  text-decoration: underline;
}

.project-meta {
  font-size: 0.625rem;
  color: var(--text-muted);
}

/* Cron */
.cron-preview {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.cron-item {
  display: grid;
  grid-template-columns: 2fr 1fr 1fr;
  gap: 1rem;
  padding: 0.75rem;
  background: var(--bg-tertiary);
  font-size: 0.75rem;
}

.cron-name {
  font-weight: 600;
}

.cron-schedule {
  color: var(--text-secondary);
}

.cron-next {
  color: var(--accent-info);
}

/* Quick Links */
.quick-links {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.quick-link {
  padding: 0.5rem 1rem;
  background: var(--bg-tertiary);
  border: 1px solid var(--border);
  color: var(--text-secondary);
  font-family: inherit;
  font-size: 0.75rem;
  cursor: pointer;
  transition: all 0.2s;
}

.quick-link:hover {
  border-color: var(--accent-info);
  color: var(--accent-info);
}

/* Cron Detail */
.cron-card {
  background: var(--bg-secondary);
  border: 1px solid var(--border);
  padding: 1.5rem;
  margin-bottom: 1rem;
}

.cron-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}

.cron-title {
  font-weight: 700;
  font-size: 1rem;
}

.cron-badge {
  font-size: 0.625rem;
  padding: 0.25rem 0.75rem;
  text-transform: uppercase;
}

.cron-badge.active {
  background: var(--accent-success);
  color: var(--bg-primary);
}

.cron-details {
  display: grid;
  gap: 0.5rem;
  font-size: 0.875rem;
}

.cron-details strong {
  color: var(--text-muted);
  display: inline-block;
  width: 100px;
}

/* Skills */
.skills-header {
  padding: 1rem;
  background: var(--bg-secondary);
  border: 1px solid var(--border);
  margin-bottom: 1rem;
  font-size: 0.875rem;
  color: var(--text-secondary);
}

.skill-category {
  background: var(--bg-secondary);
  border: 1px solid var(--border);
  padding: 1rem;
  margin-bottom: 1rem;
}

.skill-category h3 {
  font-size: 0.75rem;
  font-weight: 700;
  text-transform: uppercase;
  margin-bottom: 0.75rem;
  color: var(--text-muted);
}

.skill-list {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.skill-tag {
  padding: 0.25rem 0.5rem;
  background: var(--bg-tertiary);
  border: 1px solid var(--border);
  font-size: 0.75rem;
  color: var(--text-secondary);
}

/* Files */
.files-layout {
  display: grid;
  grid-template-columns: 250px 1fr;
  gap: 1rem;
  height: 60vh;
}

.file-tree {
  background: var(--bg-secondary);
  border: 1px solid var(--border);
  overflow-y: auto;
}

.file-tree h3 {
  padding: 0.75rem 1rem;
  font-size: 0.625rem;
  text-transform: uppercase;
  color: var(--text-muted);
  background: var(--bg-tertiary);
}

.file-tree button {
  width: 100%;
  padding: 0.75rem 1rem;
  background: transparent;
  border: none;
  border-bottom: 1px solid var(--border);
  color: var(--text-secondary);
  font-family: inherit;
  font-size: 0.75rem;
  text-align: left;
  cursor: pointer;
}

.file-tree button:hover,
.file-tree button.active {
  background: var(--bg-tertiary);
  color: var(--accent-info);
}

.file-viewer {
  background: var(--bg-secondary);
  border: 1px solid var(--border);
  overflow: auto;
}

.file-viewer pre {
  padding: 1rem;
  font-size: 0.75rem;
  line-height: 1.6;
  white-space: pre-wrap;
  word-break: break-word;
}

.file-placeholder {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  color: var(--text-muted);
}

/* Learnings */
.learnings-header {
  padding: 1rem;
  background: var(--bg-secondary);
  border: 1px solid var(--border);
  margin-bottom: 1rem;
  font-size: 0.875rem;
  color: var(--text-secondary);
}

.learning-card {
  background: var(--bg-secondary);
  border: 1px solid var(--border);
  padding: 1rem;
  margin-bottom: 1rem;
}

.learning-name {
  font-weight: 700;
  font-size: 1rem;
  margin-bottom: 0.25rem;
}

.learning-desc {
  font-size: 0.875rem;
  color: var(--text-secondary);
  margin-bottom: 0.5rem;
}

.learning-meta {
  font-size: 0.625rem;
  color: var(--text-muted);
  margin-bottom: 0.75rem;
}

.learning-card button {
  padding: 0.5rem 1rem;
  background: var(--bg-tertiary);
  border: 1px solid var(--border);
  color: var(--accent-info);
  font-family: inherit;
  font-size: 0.75rem;
  cursor: pointer;
}

.learning-card button:hover {
  background: var(--accent-info);
  color: var(--bg-primary);
}

/* Loading */
.loading {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  color: var(--text-muted);
}

/* Responsive */
@media (max-width: 768px) {
  .stats-grid {
    grid-template-columns: repeat(2, 1fr);
  }
  
  .files-layout {
    grid-template-columns: 1fr;
    height: auto;
  }
  
  .file-tree {
    max-height: 200px;
  }
}